.gh-drop {
  position: relative;
  outline: none;
  cursor: pointer
}

.gh-drop-trigger {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all .4s ease
}

.gh-drop-trigger:hover {
  transition: all .2s ease
}

.gh-dropdown {
  position: absolute;
  top: 150%;
  right: -90px;
  z-index: 900;
  margin-right: 50%;
  padding: 0;
  width: 180px;
  border: 1px solid rgba(0,0,0,.07);
  color: rgba(0,0,0,.6);
  font-size: 1.3rem;
  font-weight: 400;
  background: #fff;
  border-radius: inherit;
  border-radius: 4px;
  box-shadow: 0 2px 4px rgba(0,0,0,.038),0 0 2px rgba(0,0,0,.038);
  list-style: none;
  opacity: 0;
  transition: all .3s ease;
  transition: all .3s cubic-bezier(.4,.01,.165,.99),opacity .2s cubic-bezier(.4,.01,.165,.99);
  transform: scale(.9) translateY(-20px);
  pointer-events: none
}

.gh-dropdown:after {
  right: 83px;
  border-width: 0 6px 6px;
  border-color: #fff transparent
}

.gh-dropdown:after,.gh-dropdown:before {
  content: "";
  position: absolute;
  bottom: 100%;
  width: 0;
  height: 0;
  border-style: solid
}

.gh-dropdown:before {
  right: 81px;
  border-width: 0 8px 8px;
  border-color: rgba(0,0,0,.07) transparent
}

.gh-drop .gh-dropdown li {
  margin: 0
}

.gh-drop .gh-dropdown a {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  padding: 9px 10px;
  border-bottom: 1px solid rgba(0,0,0,.06);
  color: #738a94;
  text-decoration: none;
  transition: all .5s ease
}

.gh-drop .gh-dropdown i {
  -ms-flex-order: 2;
  order: 2;
  margin-right: 4px;
  color: inherit;
  font-size: 14px;
  text-align: center
}

.gh-dropdown li:first-of-type a {
  border-radius: 3px 3px 0 0
}

.gh-dropdown li:last-of-type a {
  border: none;
  border-radius: 0 0 3px 3px
}

.gh-dropdown .active a,.gh-dropdown a:hover {
  color: #343f44;
  transition: all .2s ease
}

.gh-drop.active .gh-dropdown,.gh-drop.dropdown-expanded .gh-dropdown {
  opacity: 1;
  transition: all .2s cubic-bezier(.4,.01,.165,.99);
  transform: scale(1) translateY(0);
  pointer-events: auto
}

.gh-dropdown.gh-dropup {
  top: auto;
  bottom: 60%;
  left: -5px;
  margin-right: 0;
  transform: scale(.9) translateY(20px)
}

.gh-dropdown.gh-dropup:after {
  left: 15px;
  border-width: 6px 6px 0;
  border-color: #fff transparent
}

.gh-dropdown.gh-dropup:after,.gh-dropdown.gh-dropup:before {
  content: "";
  position: absolute;
  top: 100%;
  right: auto;
  bottom: auto;
  width: 0;
  height: 0;
  border-style: solid
}

.gh-dropdown.gh-dropup:before {
  left: 13px;
  border-width: 8px 8px 0;
  border-color: rgba(0,0,0,.07) transparent
}

.gh-dropdown .gh-badge {
  box-shadow: none
}

