/* Code Highlight  */


code, kbd, pre, samp {
  font-family: Inconsolata, Consolas, Menlo, Monaco,"Courier New",monospace;
}

pre.prettyprint, code.prettyprint {
  color: white;
  background-color: #444;
  padding: 1em;
  line-height: 1.5;
}

pre.prettyprint.small {
  padding: 0.5em;
}

pre .tooltip {
  position: relative;
  cursor: pointer;
}

pre .tooltip .property {
  border-bottom: 1px dotted #fff;
}

pre .tooltip .property .kwd {
  color: #508B88;
}
pre .tooltip:hover:after {
content: attr(data-tooltip);
position: absolute;
background: rgba(0, 0, 0, 0.85);
padding: 5px 9px;
color: #fff;
border-radius: 3px;
left: 0;
top: 25px;
width: 390px;
z-index: 100;
}

.pln {
  color:#000
}
@media screen {
  .str {
      color:#080;
  }
  .kwd {
      color:#008;
  }
  .com {
      color:#800;
  }
  .typ {
      color:#606;
  }
  .lit {
      color:#066;
  }
  .pun,.opn,.clo {
      color:#660;
  }
  .tag {
      color:#008;
  }
  .atn {
      color:#606;
  }
  .atv {
